import { useEffect, useState } from "react";
import Recepies from "./ReceipeApp/Recepies";
import './App.css'
import { getSuggestedQuery } from "@testing-library/react";


function App() {
  const [recepies, setrecepies] = useState([]);
  const [search, setsearch] = useState("");
  const [query, setquery] = useState("chicken");


  const API_ID = "b290b41f";
  const API_KEY = " 5585d0a44d73113cc308b90ec39b809c";
  const url = `https://api.edamam.com/search?q=${query}&app_id=${API_ID}&app_key=${API_KEY}`


  useEffect(() => {

    fetch(url).then(response => {
      return response.json();
    }).then(data => {
      setrecepies(data.hits);
      console.log(data.hits)
    })

  }, [query]);

  const handleOnSubmit = (event) =>{
    event.preventDefault();
    setquery(search);
    setsearch("")
  }

  const changeHandler = (event) =>{
    setsearch(event.target.value);
  }




  return (
    <div className="app">

    <h1>Need Ingredients?</h1>
    <span>Check it out below </span>

      <form onSubmit={handleOnSubmit}>
        <input type="text" onChange={changeHandler} placeholder="Search your meal"/> <br /> 
        <input type="submit" />
      </form>
    
      <div className="recepies">
      {recepies.map(recepie => (

        
        <Recepies title={recepie.recipe.label} cuisineType={recepie.recipe.cuisineType} image={recepie.recipe.image} dietLabels={recepie.recipe.dietLabels} mealType={recepie.recipe.mealType} nutrients={recepie.recipe.totalNutrients} ingredientLines={recepie.recipe.ingredientLines}
        />
      )
        



      )}</div>

       
      
    </div>
  );
}

export default App;
