

import React, { useState } from 'react'
import './Recepies.css'

export default function Recepies({ title, cuisineType, image, mealType, nutrients, ingredientLines
}) {
  // console.log(nutrients.CA.quantity);
  const [isClciked, setisClicked] = useState(false);

  const isClickHandler = () => {
    setisClicked(!isClciked);
  }
  return (
    <div className='recepie_Card'>

      <div className='row1'>
        <div className='dishes'>
          <h2>{title}
          </h2>
          <div className='dishtype'>
            <span>Cuisine: {cuisineType}</span>
            <span>DishType: {cuisineType}</span>
            <span>mealType: {mealType}</span>
          </div>



          <div className='extra_info'>



            <li>Carbs - {nutrients.CHOCDF.quantity.toFixed()} {nutrients.CHOCDF.unit}</li>
            <li>Protein - {nutrients.P.quantity.toFixed()} {nutrients.P.unit}</li>
            <li>FAT - {nutrients.FAT.quantity.toFixed()} {nutrients.FAT.unit}</li>
          </div>
        </div>



        <img src={image} alt="" />
      </div>

      <div className='modal'>
        {isClciked &&
          <p>
            {ingredientLines.map(ingredient => (
              <li>{ingredient}</li>
            ))}
          </p>}
      </div>


      <button onClick={isClickHandler}>Check Ingredients</button>
    </div>



  )
}
